@import url(https://fonts.googleapis.com/css2?family=Caveat+Brush&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.tb-square{--padding: 0.25em;display:grid;grid-template-areas:"main";outline:1px solid #1a1433;overflow:hidden;place-items:center;-webkit-user-select:none;-ms-user-select:none;user-select:none}.tb-square:before{content:"";display:block;grid-area:main;padding-top:100%}.tb-square>.tb-square__text{grid-area:main;padding:var(--padding)}.tb-square--is-highlighted{background-color:rgba(255,0,0,.2)}
.tb-playfield{display:grid;grid-template-rows:1fr 7fr 1fr;grid-template-areas:"top" "main" "bottom"}.tb-card{display:grid;font-family:Caveat Brush;font-size:1.2em;grid-area:main;grid-template-columns:repeat(5, 1fr);margin:auto 2em;max-width:32rem;outline:2px solid #1a1433;place-content:center}.tb-star{height:auto;width:100%}.tb-star__glyph{fill:#ffb800}.tb-playfield__message{grid-area:top}.tb-playfield__confetti{bottom:0;display:grid;-webkit-filter:drop-shadow(0 48px 2px #0001);filter:drop-shadow(0 48px 2px #0001);grid-area:top;left:0;overflow:hidden;place-items:center;pointer-events:none;position:absolute;right:0;top:-30em}
.App{display:grid;height:100vh;overflow:hidden;place-items:center;text-align:center}
